@use './../global.scss' as global;

.percentage-bar {
  display: flex;
  border-radius: 5px;
  overflow: hidden;
  background-color: var(--secondary-color);

  > div {
    display: flex;
    transition: width .5s ease-out;
    animation: fill-percentage .5s ease-out;
  }
}

@keyframes fill-percentage {
  0% {
    width: 0;
  }
}