@use './../shared/global.scss' as global;

.poke-forms {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 8px;

  .total-items {
    font-weight: normal;
    opacity: .7;
    font-size: 14px;
    padding: 0 2px;
  }

  .loading-more {
    width: 100px;
    min-height: 208px;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__body {
    display: grid;
    gap: 8px;
    flex-wrap: wrap;
    grid-template-columns: repeat(auto-fit, 100%);
    box-sizing: border-box;

    .form {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: start;
      box-sizing: border-box;
      gap: 2px;
      background-color: var(--primary-color);
      border-radius: var(--global-border-radius);
      padding: 24px 0;

      a {
        width: 100%;
      }

      &:hover {
        .poke-profile__body {
          &__avatar {
            transform: scale(1.3);
          }
        }
      }

      .poke-profile {
        &__header {
          font-size: 16px;
        }

        &__body {
          > img, svg {
            transition: all ease-out .5s;
            width: 100px;
            height: 100px;
          }
        }
      }

      &.load-more {
        min-height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;
        background-color: var(--primary-color);

        cursor: pointer;
        user-select: none;
        transition: opacity 0.5s;

        &:hover {
          opacity: 0.5;
        }
      }

      &__pokemon {
        img {
          width: 100px;
          height: 100px;
        }

        &:hover {
          transition: opacity 0.5s;
          opacity: 0.5;
        }
      }
    }
  }
}